<template>
	<div id="waterfall" style="height: 1510px;">
		<div v-for="huab in huabs" v-if="huab.hid != 0" onclick="''==='pin_detail'?app.baiduTrackExposition({category: 'board', action: 'click', label: 'pin_detail'}):''" class="Board wfc " style="position: absolute; left: 0px; top: 0px;">
			<a href="/boards/17920339/" target="_blank" class="link  x">
				<img v-for="(item, index) in huab.gathers" :src="getGatherImg(item.img)" :class="{large:index==0}">

				<div class="shadows">
					<div class="large-shadow"></div>
					<div class="shadow"></div>
					<div class="shadow"></div>
					<div class="shadow"></div>
				</div>
				<div class="over ">
					<h3>{{huab.hname}}</h3>
					<h4>{{huab.describe}}</h4>
					<div class="pin-count">{{huab.gatherlength}}</div>
				</div>
			</a><span class="attr-mark"></span>
			<a href="" v-for="item in huab.gathers" title="" class="hidden"><img title="" :src="item.img"></a>
			<div class="FollowBoard2">
				<a href="/ninitoo/" :title="huab.user.uname" class="img x loaded"><img :src="getUserImg(huab.user.img)" class="BoardAvatar"></a>
				<a href="/ninitoo/" class="BoardUserUrl x">{{huab.user.uname}}</a>
				<a href="#" onclick="return false;" class="follow btn wbtn"><strong> 关注</strong><span></span></a>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "huaban",
		components: {

		},
		props: ['hits'],
		methods: {
			getUserImg: function(img) {
				return img == null ? "http://localhost/huaban/images/654953460733026a7ef6e101404055627ad51784a95c-B6OFs4_sq140sf.png" : "http://localhost/huaban/images/" + img;
			},
			getGatherImg: function(img) {
				return "http://localhost/huaban/images/" + img;
			},
			loadData:function(){
				this.axios.post("http://localhost:8080/huaban/es/search/huab/?q=" + this.$route.query.q + "&from=0&size=10").then(body => {
				var huaban = this;
				//循环取画板详细
				var hits = body.data.hits.hits;
				$.each(hits, function(i, n) {
					var huablen = huaban.huabs.length;
					huaban.axios.post("http://localhost:8080/huaban/es/search/users/?uid=" + n._source.uid).then(rey => {
						huaban.huabs[huablen].user = rey.data.hits.hits[0]._source;
					});
					huaban.axios.post("http://localhost:8080/huaban/es/search/gather/?hid=" + n._source.hid + "&from=0&size=4").then(rey => {
						var gathers = [];
						for(var i = 0; i < rey.data.hits.hits.length; i++) {
							var gather = rey.data.hits.hits[i]._source;
							gathers.push(gather);
						}
						huaban.huabs[huablen].gathers = gathers;

						huaban.huabs[huablen].gatherlength = rey.data.hits.total.value;
						console.log(rey.data.hits.hits);
						waterFall("waterfall", "Board");
					});
					var huab = {
						hid: n._source.hid,
						hname: n._source.hname,
						uid: n._source.uid,
						describe: n._source.describe,
						gatherlength: 0,
						gathers: [],
						user: {},
					}
					//赋值
					huaban.huabs.push(huab);
					
				});
			})
			}
		},
		data: function() {
			return {
				huabs: [{
					hid: 0,
					hname: "",
					uid: 0,
					uname: "",
					describe: "",
					gatherlength: 0,
					gathers: [],
					user: {
						uid: 0,
						uname: "",
						img: "",
					}
				}, ]
			};
		},
		mounted: function() {
//			setTimeout(function() {
//				waterFall("waterfall", "Board");
//			}, 500);
		},
		watch: {
			$route:function(){
				this.huabs = [];
				this.loadData();
				
			}
		},
		created() {
			this.loadData();
		}
	}

	// 瀑布流函数
	function waterFall(parent, child) {
		// 父盒子居中，取得所有父盒字
		//先获得父元素及其底下所有的子元素
		var oParent = document.getElementById(parent);
		if(oParent == null) return;
		var allBox = oParent.getElementsByClassName(child);
		//    获取子盒子高度
		//    var boxWidth = allBox[0].offsetWidth;
		var boxWidth = allBox[0].offsetWidth;
		// 求出列数
		var cols = oParent.offsetWidth / boxWidth - 1;
		var xyMargin = 16;
		//    定义高度数组
		var arrH = [];

		// 遍历盒子
		for(var i = 0; i < allBox.length; i++) {
			// 求出每个盒子的高度
			var boxH = allBox[i].offsetHeight + xyMargin;
			if(i < cols) {
				// heightArr.push(boxHeight); //第一行
				arrH.push(boxH);
				allBox[i].style.position = "absolute";
				allBox[i].style.left = i * (boxWidth + xyMargin) + 'px';
				allBox[i].style.top = xyMargin + 'px';
			} else {
				// 取出最矮的盒子高度
				var minH = Math.min.apply(null, arrH);
				//求出最矮盒子的对应所引
				var minIndex = getMinBoxIndex(arrH, minH);
				//子盒子定位
				allBox[i].style.position = 'absolute';
				allBox[i].style.left = minIndex * (boxWidth + xyMargin) + 'px';
				allBox[i].style.top = minH + xyMargin + 'px';
				arrH[minIndex] += boxH;
			}
		}
		// 更新父盒子的高度
		var parentHeight = allBox[allBox.length - 1].offsetTop + allBox[allBox.length - 1].offsetHeight;
		oParent.style.height = parentHeight;
	};
	//获取最小下标函数
	function getMinBoxIndex(arr, min) {
		for(var i = 0; i < arr.length; i++) {
			if(arr[i] === min) {
				return i;
			}
		}
	}
</script>

<style>

</style>


