<template>
	<div id="waterfall" style="height: 1137px;" @scroll="skinwheel">
		<div data-id="341535979" v-for="gather in gathers" v-if="gather.gid != 0" class="pin wfc " style="position: absolute; left: 0; top: 0; opacity: 1;">
			<div class="hidden">
				<a href="/s555888_/">{{gather.user.uname}}</a>采集到
				<a href="/boards/4036688">{{gather.hname}}</a>
			</div>
			<div class="actions">
				<div class="right">
					<a data-id="341535979" title="喜欢" href="#" onclick="return false;" class="like btn-with-icon btn btn14"><i class="heart"></i><span class="text"> {{gather.love}}</span></a>
				</div>
				<div class="left">
					<a onclick="app.requireLogin(function(){ app.showDialogBox('repin', {pin_id:'341535979'});});return false;" href="#" class="repin pin-default btn btn14 rbtn">
						<span class="text"> 采集</span></a>
					<a href="#" onclick="return false;" class="repin pin-quick btn btn14 rbtn"><img src="../../../assets/img/quick_pin.svg"></a>
				</div>
			</div>
			<a :href="'http://localhost:8080/huaban/info.html?gid='+gather.gid" target="_self" rel="" class="img x layer-view loaded">
				<div class="default-bg">pinterest.com</div><img :src="getGatherImg(gather.img)" width="236" height="100%">
				<span style="display: none" class="stop"></span>
				<div class="cover"></div>
			</a>
			<p class="description">{{gather.details}}</p>
			<p class="stats less"><span title="转采" class="repin"><i></i>{{gather.transpond}}</span><span title="喜欢" class="like"><i></i>{{gather.love}}</span></p>
			<div class="convo attribution clearfix">
				<a href="/s555888_/" :title="gather.user.uname" rel="nofollow" class="img x"><img :src="getUserImg(gather.user.img)" class="avt"></a>
				<div class="text">
					<div class="inner">
						<div class="line">
							<a href="/s555888_/" rel="" class="author x">{{gather.user.uname}}</a>&nbsp;采集到</div>
						<div class="line">
							<a href="/boards/4036688/" rel="" class="x">{{gather.hname}}</a>
						</div>
						<a title="评论" class="replyButton"></a>
					</div>
				</div>
			</div>
			<div style="display:none;" class="comments muted"></div>
			<div style="display: none;" class="write convo clearfix">
				<a href="//" title="" class="img x"><img :src="gather.img"></a>
				<form action="/pins/341535979/comments/" method="POST"><textarea placeholder="添加评论或把采集@给好友" class="clear-input GridComment"></textarea>
					<a href="#" onclick="return false;" class="grid_comment_button btn"><span class="text"> 添加评论</span></a>
				</form>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'gathers',
		computed: {

		},
		data: function() {
			return {
				pageNo:0,
				total:0,
				gathers: [{
					gid: 0,
					img: "",
					transpondUid: 0,
					transpond: 0,
					love: 0,
					hid: 0,
					date: "",
					details: "",
					hname: "",
					user: {
						uid: 0,
						uname: "",
					}
				}]
			};
		},
		created() {
			window.addEventListener('scroll', this.skinwheel, true); //加载滚动监听事件
			this.loadGather()
		},
		watch: {
			gathers: function() {
				setTimeout(function() {
					waterFall("waterfall", "wfc");
				}, 500);
			},
			$route:function(){
				this.gathers = [];
				this.loadGather();
			}
		},
		mounted: function() {

		},
		methods: {
			loadGather:function(){
				this.axios.post("http://localhost:8080/huaban/es/search/gather/?q=" + this.$route.query.q + "&from="+(this.pageNo*10)+"&size=10")
				.then(body => {
					this.total = body.data.hits.total.value;//采集总数
					console.log(1);
					var huaban = this;
					//循环取画板详细
					var hits = body.data.hits.hits;
					$.each(hits, function(i, n) {
						var huablen = huaban.gathers.length;
						huaban.axios.post("http://localhost:8080/huaban/es/search/users/?uid=" + n._source.uid).then(rey => {
							huaban.gathers[huablen].user = rey.data.hits.hits[0]._source;
						});
						var gather = {
							gid: n._source.gid,
							img: n._source.img,
							transpondUid: n._source.transpondUid,
							transpond: n._source.transpond,
							love: n._source.love,
							hid: n._source.hid,
							date: n._source.date,
							details: n._source.details,
							hname: n._source.hname,
							user: {
								uid: 0,
								uname: "",
								img: null,
							}
						}
						//						console.log(gather);
						//赋值
						huaban.gathers.push(gather);
					});
				});
			},
			getUserImg: function(img) {
				return img == null ? "http://localhost/huaban/images/654953460733026a7ef6e101404055627ad51784a95c-B6OFs4_sq140sf.png" : "http://localhost/huaban/images/" + img;
			},
			getGatherImg: function(img) {
				return "http://localhost/huaban/images/" + img;
			},
			skinwheel(event) {
				if(this.pageNo+1*10>this.total) return;
				console.log(event);
				let vm = this
				var height = $("#waterfall").height();
				console.log(height);
				console.log(event.pageY);
				if(event.pageY > height - document.documentElement.clientHeight ) {
					this.pageNo = this.pageNo+1;
					this.loadGather();
				}

			},
		},
	}

	// 瀑布流函数
	function waterFall(parent, child) {
		// 父盒子居中，取得所有父盒字
		//先获得父元素及其底下所有的子元素
		var oParent = document.getElementById(parent);
		if(oParent == null) return;
		var allBox = oParent.getElementsByClassName(child);
		//    获取子盒子高度
		//    var boxWidth = allBox[0].offsetWidth;
		var boxWidth = allBox[0].offsetWidth;
		// 求出列数
		var cols = oParent.offsetWidth / boxWidth - 1;
		var xyMargin = 16;
		//    定义高度数组
		var arrH = [];

		// 遍历盒子
		for(var i = 0; i < allBox.length; i++) {
			// 求出每个盒子的高度
			var boxH = allBox[i].offsetHeight + xyMargin;
			if(i < cols) {
				// heightArr.push(boxHeight); //第一行
				arrH.push(boxH);
				allBox[i].style.position = "absolute";
				allBox[i].style.left = i * (boxWidth + xyMargin) + 'px';
				allBox[i].style.top = xyMargin + 'px';
			} else {
				// 取出最矮的盒子高度
				var minH = Math.min.apply(null, arrH);
				//求出最矮盒子的对应所引
				var minIndex = getMinBoxIndex(arrH, minH);
				//子盒子定位
				allBox[i].style.position = 'absolute';
				allBox[i].style.left = minIndex * (boxWidth + xyMargin) + 'px';
				allBox[i].style.top = minH + xyMargin + 'px';
				arrH[minIndex] += boxH;
			}
		}
		// 更新父盒子的高度
		var parentHeight = allBox[allBox.length - 1].offsetTop + allBox[allBox.length - 1].offsetHeight;
		oParent.style.height = parentHeight;
	};
	//获取最小下标函数
	function getMinBoxIndex(arr, min) {
		for(var i = 0; i < arr.length; i++) {
			if(arr[i] === min) {
				return i;
			}
		}
	}
</script>

<style>

</style>


