<template>
	<div class="search-group">
		<form id="search_result_form" :action="'/huaban/search/index.html#'+$route.path+'?q='+context" class="search-item long_form">
			<input value="" v-model="context" placeholder="搜索你喜欢的" autocomplete="off" class="clear-input">
			<button type="submit" style="border: 0px;" class="go"></button>
		</form>

		<div class="search-hint"></div>
		<div class="search-type">
			<div id="search_switch" class="tabs">
				<router-link :to="hrefs[0]+'?q='+context" class="switch-pin tab" :class="{ active: $route.path== hrefs[0]}">{{gatherTotal}} 采集</router-link>
				<router-link :to="hrefs[1]+'?q='+context" class="switch-board tab" :class="{ active: $route.path== hrefs[1]}">{{huabTotal}} 画板</router-link>
				<router-link :to="hrefs[2]+'?q='+context" class="switch-people tab" :class="{ active: $route.path== hrefs[2]}">{{usersTotal}} 用户</router-link>
			</div>
			<div id="search_sort" class="items"><span class="category item">排序：</span>
				<a class="active item">综合</a>
				<a href="/search/boards/?q=sd%E5%A8%83%E5%A8%83&amp;type=boards&amp;sort=created_at" class="item">时间</a>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "group",
		components: {

		},
		methods: {
			//			formSubmit:function(){
			//				console.log(this.$router.path);
			//				var href = this.$router.path+"?q="+this.$router.query.q;
			//				this.$router.push({ path: href})
			//				return false;
			//			}
		},
		props: ['data_q', 'hrefs'],
		data: function() {
			return {
				context: this.data_q,
				usersTotal: 0,
				huabTotal: 0,
				gatherTotal: 0,
			}
		},
		watch: {
			data_q: function(newVal, oldVal) {
				this.context = newVal;
				this.axios.post("http://localhost:8080/huaban/es/search/users?q=" + this.$route.query.q, ).then(body => {
					this.usersTotal = body.data.hits.total.value;
				});
				this.axios.post("http://localhost:8080/huaban/es/search/huab/?q=" + this.$route.query.q + "&from=0&size=0").then(body => {
					this.huabTotal = body.data.hits.total.value;
				});
				this.axios.post("http://localhost:8080/huaban/es/search/gather/?q=" + this.$route.query.q + "&from=0&size=0").then(body => {
					this.gatherTotal = body.data.hits.total.value;
				});
			}
		},
		created: function() {
			this.axios.post("http://localhost:8080/huaban/es/search/users?q=" + this.$route.query.q, ).then(body => {
				this.usersTotal = body.data.hits.total.value;
			});
			this.axios.post("http://localhost:8080/huaban/es/search/huab/?q=" + this.$route.query.q + "&from=0&size=0").then(body => {
				this.huabTotal = body.data.hits.total.value;
			});
			this.axios.post("http://localhost:8080/huaban/es/search/gather/?q=" + this.$route.query.q + "&from=0&size=0").then(body => {
				this.gatherTotal = body.data.hits.total.value;
			});
		}
	}
</script>

<style>

</style>


