<template>
	<div id="waterfall" style="height: 1510px;">
		<div v-for="user in content.hits.hits" v-if="user._source.uname != null" class="pin user wfc" style='left: 0px; top: 0px; position: absolute;'>
			<div class="image">
				<a :title="user._source.uname" class="avatar" :href="'/hubanTow/ToDesigner.html?fdid='+user._source.uid"><img width="120" height="120" :src="getUserImg(user._source.img)"></a>
			</div>
			<div class="profile">
				<a class="username" :href="'/hubanTow/ToDesigner.html?fdid='+user._source.uid">{{user._source.uname}}</a>
				<div class="description">
					<div class="personal">
						<div class="meta">{{getSex(user._source.sex)}}</div>
					</div>
				</div>
				<div class="pins"><span class="meta">{{user._source.transpond}}采集</span><span class="meta">{{user._source.fans}}粉丝</span></div>
				<div class="buttons">
					<a class="followuser btn" onclick="return false;" href="#" data-id="guan2889947077--hh"><span class="text"> 关注</span></a>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'users',
		computed: {

		},
		methods: {
			getUserImg: function(img) {
				return img == null ? "http://localhost/huaban/images/654953460733026a7ef6e101404055627ad51784a95c-B6OFs4_sq140sf.png" : "http://localhost/huaban/images/" + img;
			},
			getSex: function(sex) {
				return sex == 1 ? "男" : sex == 2 ? "女" : "保密";
			}
		},
		data: function() {
			return {
				content:{}
			}
		},
		props: ['hits'],
		mounted: function(){
			setTimeout(function(){
				waterFall("waterfall","user");
			},500);
		},
		watch: {
			$route:function(){
				this.axios.post("http://localhost:8080/huaban/es/search/users/?q=" + this.$route.query.q, ).then(body => {
					this.content = body.data;
				})
			}
		},
		created() {
			
			this.axios.post("http://localhost:8080/huaban/es/search/users/?q=" + this.$route.query.q, ).then(body => {
				this.content = body.data;
			})
		}
	}

	// 瀑布流函数
	function waterFall(parent, child) {
		// 父盒子居中，取得所有父盒字
		//先获得父元素及其底下所有的子元素
		var oParent = document.getElementById(parent);
		if(oParent == null) return;
		var allBox = oParent.getElementsByClassName(child);
		//    获取子盒子高度
		//    var boxWidth = allBox[0].offsetWidth;
		var boxWidth = allBox[0].offsetWidth;
		// 求出列数
		var cols = oParent.offsetWidth / boxWidth - 1;
		var xyMargin = 16;
		//    定义高度数组
		var arrH = [];

		// 遍历盒子
		for(var i = 0; i < allBox.length; i++) {
			// 求出每个盒子的高度
			var boxH = allBox[i].offsetHeight + xyMargin;
			if(i < cols) {
				// heightArr.push(boxHeight); //第一行
				arrH.push(boxH);
				allBox[i].style.position = "absolute";
				allBox[i].style.left = i * (boxWidth + xyMargin) + 'px';
				allBox[i].style.top = xyMargin + 'px';
			} else {
				// 取出最矮的盒子高度
				var minH = Math.min.apply(null, arrH);
				//求出最矮盒子的对应所引
				var minIndex = getMinBoxIndex(arrH, minH);
				//子盒子定位
				allBox[i].style.position = 'absolute';
				allBox[i].style.left = minIndex * (boxWidth + xyMargin) + 'px';
				allBox[i].style.top = minH + xyMargin + 'px';
				arrH[minIndex] += boxH;
			}
		}
		// 更新父盒子的高度
		var parentHeight = allBox[allBox.length - 1].offsetTop + allBox[allBox.length - 1].offsetHeight;
		oParent.style.height = parentHeight;
	};
	//获取最小下标函数
	function getMinBoxIndex(arr, min) {
		for(var i = 0; i < arr.length; i++) {
			if(arr[i] === min) {
				return i;
			}
		}
	}
</script>

<style>

</style>


