<template>

	<div id="page" class="page-min-width page-with-header" style="display: block;">
		<div class="wrapper" style="width: 1244px;">
			<div id="ctx_bar" class="board">
				<group :data_q="$route.query.q" :hrefs="hrefs"></group>
			</div>
			<gathers v-if="$route.path == hrefs[0]"></gathers>
			<huaban v-if="$route.path == hrefs[1]"></huaban>
			<users v-if="$route.path == hrefs[2]"></users>
			
			
			<div class="loading"><img src="../../assets/loading.gif"><span>正在加载...</span></div>
		</div>
	</div>
	
</template>

<script>
	import users from "./Search-Users/users"
	import group from "./search-group/group"
	import huaban from "./search-huaban/huaban"
	import gathers from "./search-gather/gathers"

	export default {
		name: 'search',
		data: function() {
			return {
				hrefs:['/huaban/search/','/huaban/search/huab/','/huaban/search/users/'],
				content: {},
			};
		},
		components: {
			"users": users,
			"group": group,
			"huaban":huaban,
			"gathers":gathers,
		},
		methods:{
			aa:function(){
				console.log($route.path);
			}
		}
		
	}
</script>

<style>
	@import url("../../assets/css/shepherd-theme-arrows.css");
	@import url("../../assets/css/solr.css");
</style>


